html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img, picture {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.boton-amarillo {
  background-color: #e08709;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: rgb(245.2854077253, 149.2188841202, 13.2145922747);
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #e08709;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: rgb(245.2854077253, 149.2188841202, 13.2145922747);
  cursor: pointer;
}

.boton-verde {
  background-color: #71b100;
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: rgb(129.2796610169, 202.5, 0);
  cursor: pointer;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.dark-mode {
  background-color: rgb(38.25, 38.25, 38.25);
}
.dark-mode p,
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode h5,
.dark-mode h6,
.dark-mode li {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71b100;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros {
  color: #ffffff;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #ffffff;
}

/* BLOQUE imports [inicio] */
/* !BLOQUE imports [fin] */
/* BLOQUE fondo-header [inicio] */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

/* !BLOQUE fondo-header [fin] */
/* BLOQUE header [inicio] */
.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  /* subBloque header-inicio [inicio] */
  /* !subBloque header-inicio [fin] */
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  /* Mixin para pantallas tablet (usa media queries) */
  /* subBloque2 contenido-header [inicio] */
  /* !subBloque2 contenido-header [fin] */
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .contenido-header {
  /* También fija altura mínima en tablet dentro del contenido */
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

/* !BLOQUE header [fin] */
/* BLOQUE contenido-header [inicio] */
.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  /* subBloque titulo-header [inicio] */
  /* !subBloque titulo-header [fin] */
}
.contenido-header h1 {
  text-align: left;
  color: #ffffff;
  font-size: 2.5rem;
  line-height: 2.5rem;
  max-width: 400px;
}

/* !BLOQUE contenido-header [fin] */
/* --- EXPLICACIÓN EXTRA --- */
/*
& → En Sass, el símbolo & significa “en este mismo selector”.
Por ejemplo:
.header {
  &.inicio { ... }
}
es lo mismo que escribir:
.header.inicio { ... }
*/
/*
: → Se usa para pseudo-clases, que son estados especiales de los elementos.
Ejemplo: :hover (cuando pasas el mouse), :focus (cuando haces clic en un campo).
*/
/*
:: → Se usa para pseudo-elementos, que son partes virtuales creadas por CSS.
Ejemplo: ::before (algo que aparece antes), ::after (algo que aparece después).
Esto te permite añadir decoración sin agregar más elementos al HTML.
*/
/* !SECTION fin - header */
.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor--footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyrigth {
  color: #ffffff;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear height 2s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #ffffff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.anuncio {
  background-color: rgb(237.75, 237.75, 237.75);
  border: 1px solid rgb(199.5, 199.5, 199.5);
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}

.precio {
  font-weight: bold;
  color: #71b100;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

/* Si el navegador SÍ soporta WebP, usa imagen WebP (más ligera) como fondo */
.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #ffffff;
  font-weight: 900;
  font-size: 4rem;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #ffffff;
}

.testimonial {
  background-color: #71b100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #ffffff;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #ffffff;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    justify-content: center;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #e08709;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71b100;
  margin-top: 1rem;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5 rem;
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(212, 29, 16);
}